.player-video {
	position: relative;

	&__video {
		width: 100%;
		height: 100%;

		object-fit: fill;
	}
	&__play-btn {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		display: flex;
		justify-content: center;
		align-items: center;

		width: 100px;
		height: 100px;
		padding-left: 10px;
		border-radius: 50%;
		background: rgba(0, 0, 0, 0.4);

		color: white;
		font-size: 70px;
	}
}
