@import './ChatMessage.scss';

.chat {
	display: flex;
	flex-direction: column;

	width: 420px;

	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		gap: $gap-base;

		// TODO: replace fixed height value to responsive
		max-height: 540px;
		// height: 80%;
		width: 100%;
		padding: 20px 10px;
		border-radius: $border-radius-base;
		background-color: rgba($color: $clr-secondary-app, $alpha: 0.7);
	}

	&__header {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: $gap-base;

		width: 100%;
	}

	&__toggle-history-btn {
		display: none;
	}

	.logo {
		align-self: flex-end;

		width: 50px;
		height: 60px;
	}

	&__history {
		align-self: stretch;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		gap: $gap-base;

		padding: 5px;

		overflow-y: auto;
	}

	.chat-message {
		align-self: flex-start;
		flex-shrink: 0;

		&--from-me {
			align-self: flex-end;
		}
	}

	&__form {
		align-self: stretch;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		gap: $gap-small;
	}

	&__text-msg-input {
		align-self: stretch;

		min-height: 60px;
		padding: 10px 20px;
		border: 1px solid $clr-primary-app;
		border-radius: $border-radius-base;
		background-color: transparent;

		font-family: inherit;
		font-size: $font-size-small;
		color: $clr-primary-app;

		&::placeholder {
			font-family: inherit;
			color: rgba($clr-primary-app, 0.7);
		}
	}

	&__send-btn {
		align-self: flex-end;

		min-width: 120px;
		min-height: 40px;

		font-size: $font-size-base;
	}

	button.chat__send-btn {
		border: 1px solid rgba($color: $clr-primary-app, $alpha: 0.7);
	}

	&__toggle-wrapper {
		display: flex;
		justify-content: center;

		width: 100%;
		padding-top: $gap-medium;
	}

	@include fadeAnimation(200ms, 0.8, 1);
	@include popupAnimation(200ms, 0.6, 1, 50%, 0);
}

.chat .chat__wrapper {
	@include popupAnimation(150ms, 0.8, 1, 50%, 0);
}

@media screen and (max-width: $mobile-width),
	screen and (max-width: $tablet-width) and (orientation: landscape) {
	.chat {
		width: 100%;

		&__wrapper {
			max-height: 100vh;
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
		}

		&__history {
			display: none;

			&--active {
				display: flex;
			}
		}

		&__toggle-history-btn {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: $gap-small;
		}

		&__toggle-wrapper {
			display: none;
		}
	}
}
