// Adaptivity
$mobile-iphone4-width: 320px;
$mobile-width: 480px;
$tablet-width: 860px;

$gap-base: 20px;
$gap-small: calc($gap-base / 2);
$gap-medium: calc($gap-base * 2);
$gap-large: calc($gap-base * 4);

$border-radius-base: 10px;

// Colors
$clr-primary-app: white;
$clr-secondary-app: black;

// Fonts
$font-size-large: 22px;
$font-size-base: 20px;
$font-size-small: 18px;
