$startTranslateX: 50%;
$endTranslateX: 0;

.player-controls {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: $gap-base;

	width: 480px;
	height: 100vh;
	padding-left: 5px;
	background: linear-gradient(
		270deg,
		rgba(0, 0, 0, 0.7) 0%,
		rgba(0, 0, 0, 0.511) 65.1%,
		rgba(0, 0, 0, 0.385) 79.69%,
		rgba(0, 0, 0, 0.063) 100%
	);

	&__toggle-btn {
		flex-shrink: 0;
	}

	&__pane {
		align-self: flex-start;

		height: 100%;
		overflow: auto;
	}

	&__list {
		padding: 120px 40px 80px 10px;

		display: flex;
		flex-direction: column;
		gap: $gap-medium;
	}

	@include slideAnimation(200ms, $startTranslateX, $endTranslateX);
	@include fadeAnimation(400ms, 0.7, 1);
}

@media screen and (max-width: $mobile-width),
	screen and (max-width: $tablet-width) and (orientation: landscape) {
	.player-controls {
		justify-content: center;
		gap: 0;

		width: 100%;
		padding-left: 0;

		&__pane {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 100%;
		}

		&__list {
			padding: 80px 20px 40px 20px;
		}
	}

	button.player-controls__toggle-btn {
		display: none;
	}
}
