$track-height: 5px;

$thumb-height: 15px;
$thumb-width: 15px;

@mixin trackStyling() {
	height: $track-height;
	border-radius: 5px;
	background: white;
}

@mixin thumbStyling() {
	height: $thumb-height;
	width: $thumb-width;
	border-radius: 50%;
	background-color: white;
}

.VolumeControl {
	display: flex;
	align-items: center;
	gap: 10px;
}

.VolumeControlInput {
	width: 200px;
	background: transparent;
	-webkit-appearance: none;
	appearance: none;
	cursor: pointer;

	&::-webkit-slider-runnable-track {
		@include trackStyling;
	}

	&::-moz-range-track {
		@include trackStyling;
	}

	&::-webkit-slider-thumb {
		margin-top: calc($track-height / 2) - calc($thumb-height / 2); /* Centers thumb on the track */
		-webkit-appearance: none; /* Override default look */
		appearance: none;

		@include thumbStyling;
	}

	&::-moz-range-thumb {
		border: none; /*Removes extra border that FF applies*/

		@include thumbStyling;
	}
}
