@import './PlayerVideo.scss';
@import './PlayerControls.scss';
@import './PlayerControlsList.scss';
@import './PlayerControlsItem.scss';

$startTranslateX: 75%;
$startTranslateXMobile: 100%;
$endTranslateX: 0;

.player-page {
	position: relative;
	height: 100%;

	&__video {
		width: 100vw;
		height: 100%;
	}

	&__controls {
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 2147483647;

		max-height: 100vh;
		transform: translateX($startTranslateX);
	}

	&__chat {
		position: absolute;
		bottom: $gap-medium;
		left: 50%;

		transform: translateX(-50%);
	}
}

@include slideAnimation(250ms, $startTranslateX, $endTranslateX);

@media screen and (max-width: $mobile-width),
	screen and (max-width: $tablet-width) and (orientation: landscape) {
	.player-page {
		&__controls {
			transform: translateX($startTranslateXMobile);
		}

		&__chat {
			bottom: 0;
		}
	}

	@include slideAnimation(200ms, $startTranslateXMobile, $endTranslateX);
}
