@import './_index.scss';
@import './PlayerPage.scss';
@import './Chat.scss';

@include scrollbar;

* {
	box-sizing: border-box;
}

body {
	touch-action: pan-y;
}

body,
html,
#root {
	height: 100%;
	min-height: -webkit-fill-available;
	min-height: -moz-available;

	overflow: hidden;
}

.App {
	height: 100%;
}

body {
	font-size: $font-size-base;
	font-family: 'Roboto', sans-serif;
	color: $clr-primary-app;

	&::-webkit-scrollbar {
		display: none;
	}
}

button {
	padding: 0;
	background: none;
	border: none;

	color: $clr-primary-app;

	&:hover {
		cursor: pointer;
	}
}
